import uniIcon from '@/components/uni-icon/uni-icon.vue';
export default {
	components: {
		uniIcon
	},
	data() {
		return {
			title: 'Hello',
			lunboList: [],
			hotList: [],
			cateMovieA1: [],
			cateMovieA2: [],
			cateMovieA3: [],
			type_name_ch1: [],
			type_name_ch2: [],
			type_name_ch3: [],
			picUrl: this.$pictureUrl,
			statusBarHeight: 0,
			keyword: ''
		}
	},
	onLoad() {
		// 取轮播图数据
		this.selectLunbo();
		// 取热门强推
		this.selectTypeMovie(1);
		// 按类型取电影
		this.selectCateMovie('A1');
		this.selectCateMovie('A2');
		this.selectCateMovie('A3');
	},
	onPullDownRefresh() {
		console.log('下拉刷新')
		// 取轮播图数据
		this.selectLunbo();
		// 取热门强推
		this.selectTypeMovie(1);
		// 按类型取电影
		this.selectCateMovie('A1');
		this.selectCateMovie('A2');
		this.selectCateMovie('A3');
	},
	methods: {
		transform() { // 换一换
			console.log('换一换')
		},
		toDetails(movie_id, score) { // 跳转详情
			uni.navigateTo({
				url: '../details/details?movie_id=' + movie_id + '&score=' + score
			});
		},
		selectLunbo() { // 取轮播图数据
			uni.request({
				url: this.$serverUrl + '/api/index/selectLunbo',
				success: (ret) => {
					if (ret.statusCode !== 200) {
						console.log('请求失败', ret);
						return;
					}
					if (ret.data.code == 0) {
						// 取数据并赋值
						this.lunboList = ret.data.data.list;
					} else {
						console.log('未取得数据');
					}
					uni.stopPullDownRefresh();
				}
			});
		},
		selectTypeMovie(type) { // 取热门强推
			uni.request({
				url: this.$serverUrl + '/api/index/selectTypeMovie?type=' + type,
				success: (ret) => {
					if (ret.statusCode !== 200) {
						console.log('请求失败', ret);
						return;
					}
					if (ret.data.code == 0) {
						// 取数据并赋值
						this.hotList = ret.data.data.list;
					} else {
						console.log('未取得数据');
					}
					uni.stopPullDownRefresh();
				}
			});
		},
		selectCateMovie(index) { // 按类型取电影
			uni.request({
				url: this.$serverUrl + '/api/index/selectCateMovie?index=' + index,
				success: (ret) => {
					if (ret.statusCode !== 200) {
						console.log('请求失败', ret);
						return;
					}
					if (ret.data.code == 0) {
						// 取数据并赋值
						if (index == 'A1') {
							this.cateMovieA1 = ret.data.data.list;
							this.type_name_ch1 = ret.data.data.type_name_ch;
						}
						if (index == 'A2') {
							this.cateMovieA2 = ret.data.data.list;
							this.type_name_ch2 = ret.data.data.type_name_ch;
						}
						if (index == 'A3') {
							this.cateMovieA3 = ret.data.data.list;
							this.type_name_ch3 = ret.data.data.type_name_ch;
						}
					} else {
						console.log('未取得数据');
					}
					uni.stopPullDownRefresh();
				}
			});
		},
		closeIt(obj) {
			if (obj == 'close') {
				console.log(123);
				uni.setStorageSync('search_flag', true);
				uni.setStorageSync('search_keyword', '');
				// uni.navigateBack({
				// 	delta: 1
				// })
			}
			if (obj == 'search') {
				let keyword = this.keyword;
				console.log('存入缓存：'+keyword)
				uni.setStorageSync('search_flag', true);
				uni.setStorageSync('search_keyword', keyword);
				// uni.navigateBack({
				// 	delta: 1
				// })
			}
		}
	}
}
